import {BehaviorSubject } from 'rxjs';

export default class AuthStream {

    private static tokenStream = new BehaviorSubject('');
    public static $token = AuthStream.tokenStream.asObservable();

    static setToken(newToken: string) {
        this.tokenStream.next(newToken);
    }

    private static userStream = new BehaviorSubject('');
    public static $user = AuthStream.userStream.asObservable();

    static setUser(newUser: any) {
        this.userStream.next(newUser);
    }
}