import React from 'react';
import UserProfileLogin from './UserProfileLogin';
import UserProfileDropdown from './UserProfileDropdown';
import AuthStream from '../../util/AuthStream';
import { Subscription } from 'rxjs';
import UserProfileRegister from './UserProfileRegister';

export interface UserProfileComponentProps {
    
}
 
export interface UserProfileComponentState {
    isLoggedIn: boolean,
    tokenSubscription: Subscription | undefined,
    token: string,
    userSubscription: Subscription | undefined,
    user: any
}
 
class UserProfileComponent extends React.Component<UserProfileComponentProps, UserProfileComponentState> {
    constructor(props: UserProfileComponentProps) {
        super(props);
        this.state = {
            isLoggedIn: false,
            tokenSubscription: undefined,
            token: '',
            userSubscription: undefined,
            user : undefined,

        }
    }

    componentDidMount = () => {
        const tokenSubscription = AuthStream.$token.subscribe(token => {
            if (token) {
                this.setState({
                    isLoggedIn: true,
                    token: token
                })
            } else {
                this.setState({
                    isLoggedIn: false
                })
            }
        })

        const userSubcription = AuthStream.$user.subscribe(user => {
            if (user) {
                this.setState({
                    user: user
                })
            }
        })

        this.setState({
            tokenSubscription: tokenSubscription,
            userSubscription: userSubcription
        })
    }

    componentWillUnmount = () => {
        const tokenSub = this.state.tokenSubscription;
        tokenSub && tokenSub.unsubscribe();
        const userSub = this.state.userSubscription;
        userSub && userSub.unsubscribe();
    }
    
    render() { 
        if (this.state.isLoggedIn){
            // The user is already logged in. Give them access to their profile, and tickets
            return ( 
                <div className="ml-auto navbar-nav inline" id="user-profile-auth">
                    <UserProfileDropdown />
                </div>
                );
        } else {
            // User needs to log in. Give them a login modal and toggle button.
            return (
                <div className="ml-auto navbar-nav inline" id="user-profile-auth">
                    <UserProfileLogin />
                    <UserProfileRegister />
                </div>
                );
        }
    }
}
 
export default UserProfileComponent;